<template>
  <div class="details">
    <el-tabs type="card">
      <el-tab-pane label="Info">
        <vue-markdown :source="info" />
      </el-tab-pane>
      <el-tab-pane label="CheatSheets">CheatSheets</el-tab-pane>
      <el-tab-pane label="Tutorials">Tutorials</el-tab-pane>
      <el-tab-pane label="Links">Links</el-tab-pane>
      <el-tab-pane label="Videos">Videos</el-tab-pane>
      <el-tab-pane label="Books">Books</el-tab-pane>
</el-tabs>
  </div>
</template>
<style>
.details {
  text-align: left;
}
</style>

<script>
// var VueMarkdown = require('vue-markdown');


function GetUrl(yourUrl){
    var Httpreq = new XMLHttpRequest(); // a new request
    Httpreq.open("GET",yourUrl,false);
    Httpreq.send(null);
    return Httpreq.responseText;          
  }


export default {
  components: {
      'vue-markdown': require('vue-markdown').default,
      // editor: require('vue2-ace-editor'),
  },
  props: ['node'],
  data() {
    return {
      info: ''
    } 
  },
  mounted() {
    this.update_node();
  },
  methods: {
    update_node() {
      if(this.node.info!=undefined) this.update_info(this.node.info);
    },
    update_info(source) {
      this.info = GetUrl(source);
      // console.log();
    }
  },
  watch: { 
    node: function(newVal, oldVal) { // watch it
      if(newVal!=oldVal) {
        this.update_node();
      }
      // console.log('Prop changed: ', newVal, ' | was: ', oldVal)
    }
  }
}
</script>

