<template>
  <div id="app">

      <el-dialog :title="node.label" 
    :fullscreen="true"
    :visible.sync="dialog_visible">
        <element-details :node="node"/>
    </el-dialog>
    <!-- <div>
      <el-button><i class="fab fa-accessible-icon"></i>Hello</el-button>

    </div> -->


    <!-- <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div> -->
    <!-- <router-view/> -->
    <universe/>
  </div>
</template>

<script>
import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import Universe from './views/Universe';
import Details from './views/Details';
import "@fortawesome/fontawesome-free/js/all.min.js";
import Node from './node.js';

Vue.use(ElementUI);

export default {
  components: {
    'universe': Universe,
    'element-details': Details
  },
  mounted() {
    this.node = new Node("#universe", './json/universe.json', this.on_click);
    this.node.init();
  },
  data() {
    return {
      dialog_visible: false,
      node: {}
    }
  },
  methods: {
    on_click(node) {
      if(node.info!="") {
   this.node = node;
      this.dialog_visible = true;
      }
   
    }
  }
  
}
</script>

